import React, { useEffect }from 'react';
import './App.css';
import TVChannels from './components/TVChannels';
import Main from './components/Main';
import { CssBaseline, ThemeProvider, createTheme, styled } from '@mui/material';
import useMediaQuery from '@mui/material/useMediaQuery';

const App: React.FC = () => {
  // const theme = useTheme();
  const xs = useMediaQuery('(min-width:420px)');
  const sm = useMediaQuery('(min-width:600px)');
  const md = useMediaQuery('(min-width:1200px)');
  const GlobalStyle = styled('div')(({ theme }) => ({
    '& .MuiCssBaseline-root': {
      backgroundColor: theme.palette.background.default,
      color: theme.palette.text.primary,
    },
    'html, body, #root, #app': { // Depending on the ID of your root div
      height: '100%',
      backgroundColor: theme.palette.background.default,
    }
  }));
  

  const theme = createTheme({
    palette: {
      mode: 'dark',
      background: {
        default: '#000',  // Adjust this value as per your preference
        paper: '#4141412b', // Adjust this value as per your preference
      },
    },
    typography: {      
      fontSize: 12,
    },
    components: {
      MuiUseMediaQuery: {
        defaultProps: {
          noSsr: true,
        },
      },
    },
  });
 
  useEffect(() => {
    const loadingScreen = document.querySelector('.loading-screen');
    if (loadingScreen) {
      (loadingScreen as HTMLElement).style.display = 'none';
    }
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyle />
      <CssBaseline />      
      <TVChannels m3u8Url="./iptv.m3u8">
           <Main/>
           <header className="App-header">
              <img style={{ width: xs ? (sm?(md?"26rem":"20rem"):"14rem"):"10rem" }} src="logo.png" alt="Greek TV App Logo"/>
          </header>
        </TVChannels>
  </ThemeProvider>  
  );
}

export default App;
