import React, { useContext } from "react";
import { Box, Stack, Typography } from "@mui/material";
import Progress from "./Progress";
import { TVChannelsContext } from "./TVChannels"; // Assuming this is your context import path
import { calcDurationPerc, formatTime, remainingTime } from "../utils";
import useMediaQuery from '@mui/material/useMediaQuery';

type ChannelOverlayProps = {
  topMenu?: Boolean; // Using optional prop, set it to true if it's required
};

const ChannelOverlay: React.FC<ChannelOverlayProps> = ({ topMenu }) => {
  const context = useContext(TVChannelsContext);
  if (!context) {
    throw new Error("ChannelOverlay must be used within a TVChannels provider");
  }
  // const theme = useTheme();
  const xs = useMediaQuery('(min-width:420px)');
  const sm = useMediaQuery('(min-width:600px)');
  const md = useMediaQuery('(min-width:1200px)');
  
  const hs = useMediaQuery('(min-height:300px)');
  const hm = useMediaQuery('(min-height:500px)');
  const hh = useMediaQuery('(min-height:700px)');
  
  const { currentChannel } = context;
  // console.log("CurrentChannel Program:", currentChannel?.currentProgram);
  const program = currentChannel?.currentProgram;
  const programInfo = program?.getProgramInfo();
  const programStart = program?.start;
  const programStop = program?.stop;
  //console.log("Overlay:",program,programInfo);
  // var channel.currentProgram.getProgramInfo()
  return (
    <>
      {currentChannel && program?.channel &&(
        <Box
          sx={{
            position: "fixed",
            bottom: "1rem",
            left: "1.7rem",
            right: topMenu ? "18rem" : "2rem",
            height:"25vh",
            minHeight:"100px",            
            backgroundColor: "rgba(0, 0, 0, 0.2)", // semi-transparent black
            backdropFilter: "blur(2px)",
            padding: 2,
            borderRadius: 2,
            display: "flex",
            alignItems: "center",
          }}
        >
          {xs && currentChannel?.iconSrc && (
            <Box sx={{ width: xs ? (sm?(md?"12rem":"8rem"):"6rem"):"3rem" }}>
              <Stack>
                <img
                  src={currentChannel.iconSrc}
                  alt={`${currentChannel.name} Icon`}                  
                  style={{}} // you can adjust the width and margin as needed
                />
              </Stack>
            </Box>
          )}
          <div>
            {!xs && currentChannel?.iconSrc && (<img src={currentChannel.iconSrc}
                          alt={`${currentChannel.name} Icon`}
                          style={{width:"4rem"}} // you can adjust the width and margin as needed
                />)}
            <Typography variant="h5">{programInfo?.title}</Typography>
            <Progress
              value={calcDurationPerc(programStart || new Date(), programStop)}
              start={formatTime(programStart)}
              label={remainingTime(programStop)}
            />
            <div
              style={{
                overflow: "scroll",
                textOverflow: "ellipsis",
                width: "100%",
                height: "9vh",
                minHeight:"1rem"
              }}
            >
              <Typography variant="body2">{programInfo?.desc}</Typography>
            </div>
            <Typography variant="body2" style={{overflow:"hidden",width:"100%",textOverflow: "ellipsis",height:"1rem"}}>{programInfo?.subTitle}</Typography>
          </div>
        </Box>
      )}
    </>
  );
};

export default ChannelOverlay;
