import React, { useContext, useEffect, useState } from "react";
import {
  Avatar,
  DialogContent,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  useMediaQuery,
} from "@mui/material";
import { LiveTv as LiveTvIcon } from "@mui/icons-material";
import Channel from "../classes/Channel";
import { TVChannelsContext } from "./TVChannels";

type ChannelListProps = {
  channels: Channel[]; // Assuming you have a type for Channel
  onChannelClick: (channel: Channel) => void;
  onShow: (open: Boolean) => void;
  userInactive: Boolean;
};

const ChannelList: React.FC<ChannelListProps> = ({
  channels,
  onChannelClick,
  onShow,
  userInactive,
}) => {
  const context = useContext(TVChannelsContext);
  const [menuOpen, setMenuOpen] = useState(false);

  if (!context) {
    throw new Error("ChannelOverlay must be used within a TVChannels provider");
  }
  const { currentChannel } = context;
  
  // const theme = useTheme();
  const xs = useMediaQuery('(min-width:420px)');
  const sm = useMediaQuery('(min-width:900px)');
  // const md = useMediaQuery('(min-width:1200px)');

  useEffect(() => {
    onShow(menuOpen);
  }, [onShow, menuOpen]);

  return (
    <>
      <IconButton
        // style={{ position: "absolute", top: 10, right: 10, zIndex: 10 }}

        size="large"        
        sx={{
          position: "fixed",
          top: sm ? "42%":  xs ? "10%" :"10%",
          right: xs ? 10:5,
          zIndex: 10,
          backdropFilter:"blur(5px)",
          background:"#2cb2ff6f",
          ...((menuOpen || userInactive) && { display: "none" }),
        }}
        onClick={() => setMenuOpen(!menuOpen)}
      >
        <LiveTvIcon />
      </IconButton>
      <Drawer
        anchor="right"
        open={menuOpen}
        variant="temporary"
        ModalProps={{ onClose: (ev, reason) => setMenuOpen(false) }}
      >
        <DialogContent sx={{ paddingLeft: "1rem" }}>
          <List>
            {channels.map((channel) => (
              <ListItem
                divider
                button
                key={channel.id}
                onClick={() => {
                  onChannelClick(channel);
                  setMenuOpen(false);
                }}
                sx={{ paddingLeft: "0.1rem" }}
                autoFocus={currentChannel && currentChannel.id === channel.id
                  ? true
                  : false}
                selected={
                  currentChannel && currentChannel.id === channel.id
                    ? true
                    : false
                }
              >
                <ListItemAvatar sx={{ mr: "0.6rem" }}>
                  {channel.iconSrc ? (
                    <Avatar
                      variant="square"
                      src={channel.iconSrc}
                      alt={`${channel.name} Icon`}
                      sx={{ width: 64, height: 44 }}
                      imgProps={{
                        style: {
                          maxHeight: "100%",
                          maxWidth: "100%",
                          objectFit: "contain",
                        },
                      }}
                    />
                  ) : (
                    <Avatar>{channel.name.charAt(0)}</Avatar>
                  )}
                </ListItemAvatar>
                <ListItemText
                  secondaryTypographyProps={{
                    noWrap: true,
                    sx: { fontSize: 9 },
                  }}
                  primary={channel.name}
                  secondary={channel.currentProgram.getProgramInfo().title}
                  sx={{
                    maxWidth: 146,
                    width: 146,
                  }}
                />
              </ListItem>
            ))}
          </List>
        </DialogContent>
      </Drawer>
    </>
  );
};

export default ChannelList;
