type Book = String; // Replace with the actual book type if you have one

interface State {
  books: Book[];
}

interface Action {
  type: string;
  book?: Book;
}

const initState: State = {
  books: []
};

const reducer = (state: State = initState, action: Action): State => {
  switch (action.type) {
    case "ADD_BOOK": {
      return {
        ...state,
        books: state.books.concat(action.book!)
      };
    }
    default: {
      return state;
    }
  }
};

export default reducer;
